.flex {
  display: flex !important;
}

.inline-block {
  display: inline-block !important;
}

.hidden {
  display: none !important;
}

.visible {
  display: block !important;
}

.fade-in {
  opacity: 0;
  animation: fadeIn 0.5s forwards;
}

.fade-out {
  opacity: 1;
  animation: fadeOut 0.5s forwards;
}

.wobble {
  animation: wobble 0.5s ease-in-out infinite;
}

.fade-in-wobble {
  animation: fadeIn 0.7s ease-in, wobble 0.5s ease-in-out infinite;
}

.glow {
  animation: click-glow 0.7s ease-in-out;
}

.highlight {
  animation: fadeIn 0.7s ease-in-out, highlights 5s ease-in-out infinite;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

@keyframes pulse {
  0%, 100% {
    transform: scale(1);
  }
  50% {
    transform: scale(0.8);
  }
}

@keyframes wobble {
  0%,
  100% {
    transform: rotate(0deg);
  }
  25% {
    transform: rotate(-2.0deg);
  }
  75% {
    transform: rotate(2.0deg);
  }
}

@keyframes click-glow {
  0% {
    opacity: 0;
    box-shadow: 0 0 15px rgba(1, 230, 255, 0.8), 0 0 30px rgba(5, 247, 211, 0.4);
  }
  50% {
    opacity: 0.5;
    box-shadow: 0 0 30px rgba(51, 246, 126, 0.6), 0 0 50px rgba(0, 255, 34, 0.3);
  }
  100% {
    opacity: 1;
    box-shadow: 0 0 10px rgba(255, 163, 16, 0.5), 0 0 20px rgba(239, 82, 168, 0.2);
  }
}

@keyframes highlights {
  0%, 100% {
    box-shadow: 0 0 20px rgb(255, 0, 0,1);
    transform: scale(1);
  }
  50% {
    box-shadow: 0 0 40px rgba(255, 0, 0, 1);
    transform: scale(0.9);
  }
}